body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
@font-face{
  font-family: "suit-regular";
  src: url("./assets/fonts/SUIT-Regular.ttf") format("truetype");
}
@font-face{
  font-family: "suit-L";
  src: url("./assets/fonts/SUIT-Light.ttf") format("truetype");
}
@font-face{
  font-family: "suit-EL";
  src: url("./assets/fonts/SUIT-ExtraLight.ttf") format("truetype");
}
@font-face{
  font-family: "ramaraja";
  src: url("./assets/fonts/Ramaraja-Regular.ttf") format("truetype");
}